let ownlist = [];
function ownreducer(state=ownlist,action){
    if(action.type=='ADD'){
        return [...state,action.value]
    }
    if(action.type=='DEL'){
        let arr = [...state];
        arr.splice(action.index,1);
        return arr;
    }
    return state;
}
export default ownreducer;