import React from 'react';
import {TabBar} from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Bottom extends React.Component {
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: 120,
                bottom:10
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="gray"
                    barTintColor="white"
                >
                {/* 店铺 */}
                <TabBar.Item
                    title="店铺"
                    key="shop"
                    icon={
                        <i className='iconfont icon-dianpu'></i>                           
                    }
                    selectedIcon={
                        <i className='iconfont icon-dianpu1'></i>                           
                    }
                    data-seed="logId"
                ></TabBar.Item>
                {/* 客服 */}
                <TabBar.Item
                    icon={
                        <i className='iconfont icon-kefu1'></i>                           
                    }
                    selectedIcon={
                        <i className='iconfont icon-kefu'></i>                           
                    }
                    title="客服"
                    key="server"
                    data-seed="logId1"
                ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Bottom);