import React from 'react';
import Mytab from './Mytab';
import { connect } from 'react-redux';
import './Home.css';
const Own = (props) => {
    if(props.username==''){
        props.history.push('/login')
    }
    let goods=props.ownlist;
    return (
        <div>
            {(goods==0)?null:
            goods.map((goods,key)=>{
                return(
                    <div className="as">
                        <img className="pic" src={goods.img} alt=''/>
                        <p>{goods.title}</p>
                        <p style={{color:'gray'}}>{goods.price}</p>
                        <button className='can' onClick={()=>{
                            props.dispatch({
                                type:'DEL',
                                index:key
                            })
                        }}>取消收藏</button>
                    </div>
                )
            })}
            <Mytab/>
        </div>
    )
}
const mapStateToProps=(state)=>({
    username:state.login.username,
    ownlist:state.ownlist
})
export default connect(mapStateToProps)(Own);