import React from 'react';
import './Search.css';
import { SearchBar} from 'antd-mobile';
class search extends React.Component{
    componentDidMount() {
        this.autoFocusInst.focus();
    }
    render(){
        return(
            <div>
                <SearchBar 
                placeholder="Search"
                onCancel={() =>this.props.history.go(-1)}
                ref={ref => this.autoFocusInst = ref} 
                showCancelButton/>
            </div>
        )
    }
}
export default search;