import React from 'react';
import Bottom from './Bottom';
import { connect } from 'react-redux';
import './store.css';
const detail=(props)=>{
    let good=props.location.state.goods;
    let img=good.img
    let t = 0;
    for(let i = 0;i < props.ownlist.length;i++){
        if(good.id==props.ownlist[i].id){
            t = 1;
        }
    }
    function add(){
        if(t==0){
            props.dispatch({
                type:'ADD',
                value:good
            })
        }
    }
    return(
        <div>
            <i style={{fontSize:50,display:"block"}} 
            className='iconfont icon-fanhui'
            onClick={() =>props.history.go(-1)}></i>
            <img src={img}
            style={{height:300,width:300,margin:40}}/>
            <div>
                <Bottom/>
                <button className='button3' 
                onClick={()=>{
                    (props.username=='')?props.history.push('/login'):
                    add() 
                }}>
                    {
                        (t==0)?<i className='iconfont icon-shoucang1'></i>
                        :<i className='iconfont icon-shoucang'></i>
                    }
                    <p>收藏</p>
                </button>
                <button className='button1'>加入购物车</button>
                <button className='button2'>立即购买</button>
            </div>
        </div>
    )
}
const mapStateToProps=(state)=>({
    username:state.login.username,
    ownlist:state.ownlist
})
export default connect(mapStateToProps)(detail);