import React from 'react';
import './Home.css';
import { Carousel} from 'antd-mobile';
import {NavLink} from 'react-router-dom'
import Mytab from './Mytab';
import {Things} from './Things'
import { connect } from 'react-redux';
const Home=(props)=> {
    props.dispatch(Things());
    return <div className='box'>
        {/* 导航头部 */}
        <div className='nav'>
            <div className='taobao'>
                <i className='iconfont icon-taobao'></i>
            </div>
            <NavLink to='/search'>
                <div className='search'>
                    <i className='iconfont icon-search'></i>
                    <p>寻找宝贝店铺</p>
                </div>
            </NavLink>
        </div>
        {/* 轮播图 */}
        <div className='lunbotu'>
            <Carousel autoplay infinite>
                <img src='https://gw.alicdn.com/imgextra/i3/111/O1CN01fhP0rv1CgsatV3Imq_!!111-0-lubanu.jpg' alt="" />
                <img src='https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg' alt="" />
                <img src='https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg' alt="" />
                <img src='https://gw.alicdn.com/imgextra/i1/53/O1CN01GanI2E1CGJcfKOxML_!!53-0-lubanu.jpg' alt="" />
                <img src='https://gw.alicdn.com/imgextra/i3/29/O1CN01wYa8xO1C5K7Ka9quz_!!29-0-lubanu.jpg' alt="" />
                <img src='https://gw.alicdn.com/imgextra/i1/123/O1CN01hzfrBq1CmNLdUT99q_!!123-0-lubanu.jpg' alt="" />
                <img src='https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i2/6000000006904/O1CN01dLPgGK20s54ErU225_!!6000000006904-0-octopus.jpg' alt="" />
                <img src='https://gw.alicdn.com/imgextra/i3/189/O1CN0101BLCD1DGbUJJRfeY_!!189-0-lubanu.jpg' alt="" />
            </Carousel>
        </div>
        {/* 10张图片 */}
        <div className='con'>
            {
                (props.goods==0)?null:
                props.goods.map((goods,key)=>{
                    return(
                        <NavLink to={{pathname:'/detail',state:{goods}}}>
                        <div className="a">
                            <img className="pic" src={goods.img} alt='' key={key}/>
                            <p key={key+10}>{goods.title}</p>
                            <p style={{color:'gray'}} key={key+20}>{goods.price}</p>
                        </div>
                        </NavLink>
                    )
                })
            }
        </div>
        <Mytab/>
    </div>
}
const mapStateToProps=(state)=>({
    goods:state.home.goods
})
export default connect(mapStateToProps)(Home);