import React from 'react';
import {TabBar} from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="gray"
                    barTintColor="white"
                >
                {/* 首页 */}
                <TabBar.Item
                    title="首页"
                    key="home"
                    icon={
                        <i className='iconfont icon-shouye'></i>                           
                    }
                    selectedIcon={
                        <i className='iconfont icon-shouye1'></i>                           
                    }
                    selected={ pathname === '/' }
                    onPress={ () => {
                        this.props.history.push('/');
                    } }
                    data-seed="logId"
                ></TabBar.Item>
                {/* 购物车 */}
                <TabBar.Item
                    icon={
                        <i className='iconfont icon-shoucang1'></i>                           
                    }
                    selectedIcon={
                        <i className='iconfont icon-shoucang'></i>                           
                    }
                    title="收藏"
                    key="own"
                    selected={ pathname === '/own' }
                    onPress={ () => {
                        this.props.history.push('/own')
                    } }
                    data-seed="logId1"
                ></TabBar.Item>
                {/* 订单 */}
                <TabBar.Item
                    icon={
                        <i className='iconfont icon-dingdan1'></i>
                    }
                    selectedIcon={
                        <i className='iconfont icon-dingdan'></i>
                    }
                    title="订单列表"
                    key="order"
                    selected={ pathname==='/order' }
                    onPress={ () => {
                        this.props.history.push('/order');
                    } }
                    data-seed="logId2"
                ></TabBar.Item>
                {/* 我的淘宝 */}
                <TabBar.Item
                    icon={
                        <i className='iconfont icon-yonghu'></i> 
                    }
                    selectedIcon={ 
                        <i className='iconfont icon-user'></i> 
                    }
                    title="我的淘宝"
                    key="my"
                    selected={ pathname === '/My' }
                    onPress={ () => {
                        this.props.history.push('/My');
                    } }
                    data-seed="logId3"
                ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);